/* ‚úÖ Style global */
.auth-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to right, #F2E3D0, #BFA68F, #D9C7B4); /* D√©grad√© beige personnalis√© */
}

/* ‚úÖ Bo√Æte principale */
.auth-card {
    display: flex;
    width: 900px;  /* ‚úÖ Ajuste la taille du cadre */
    background: white;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

/* ‚úÖ Partie gauche avec l'image */
.auth-left {
    width: 50%;
    
    background-image: url('../assets/login-bg.jpg');
    background-image: url('../assets/signup-bg.jpg');

    background-size: cover;
}
.auth-left img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* ‚úÖ Partie droite avec le formulaire */
.auth-right {
    width: 50%;
    padding: 40px;
    text-align: center;
}

/* ‚úÖ Style du nom de la marque LYS√âA */
.brand-title {
    font-size: 32px;
    font-weight: bold;
    color: #BFA68F; /* ‚úÖ M√™me couleur que Cosmetic */
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 10px;
}

/* ‚úÖ Style du formulaire */
.auth-right h2 {
    font-size: 22px;
    color: #7a645f;
    margin-bottom: 20px;
}

.auth-right input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* ‚úÖ Bouton */
.auth-right button {
    width: 100%;
    padding: 12px;
    background: #bb7a5a;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;

}

.auth-right button:hover {
    background: #a76647;
}

/* ‚úÖ Texte sous le bouton */
.auth-note {
    font-size: 14px;
    color: #7a645f;
    margin-top: 10px;
    text-align: center;
    font-style: italic;
}
/* ‚úÖ Ajustement pour Lys√©a */
.brand-name {
    font-size: 60px; /* ‚úÖ M√™me taille que "COSMETIC" */
    font-weight: bold; /* ‚úÖ Gras */
    color: #F2E3D0 ; /* ‚úÖ M√™me couleur */
    text-transform: uppercase; /* ‚úÖ Majuscules */
    letter-spacing: 5px; /* ‚úÖ Espacement des lettres similaire */
    font-family: "Montserrat", sans-serif; /* ‚úÖ Police similaire */
    text-align: center; /* ‚úÖ Centr√© */
    margin-bottom: 5px; /* ‚úÖ Espacement ajust√© */
}
.error-message {
    color: red;
    font-weight: bold;
    text-align: center;
    background: rgba(255, 0, 0, 0.08);
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
}


