/* ‚úÖ Grille des produits */
.product-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px; /* ‚úÖ Espacement uniforme */
    padding: 20px;
}

/* ‚úÖ Carte produit avec hauteur fixe */
.product-card {
    width: 250px; /* ‚úÖ Taille uniforme */
    height: 450px; /* ‚úÖ Hauteur fixe pour √©viter les √©carts */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between; /* ‚úÖ Pour bien espacer les √©l√©ments */
    background: rgb(223, 215, 219);
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease;
}

.product-card:hover {
    transform: scale(1.05);
}

/* ‚úÖ Image du produit avec taille uniforme */
.product-card img {
    width: 100%;
    height: 200px; /* ‚úÖ Assure une taille d'image identique */
    object-fit: contain; /* ‚úÖ Maintient les proportions sans d√©formation */
}

/* ‚úÖ Nom du produit */
.product-card h3 {
    font-size: 16px;
    font-weight: bold;
    color: #333;
    flex-grow: 1; /* ‚úÖ Permet au texte de s'√©tendre uniform√©ment */
}

/* ‚úÖ Prix */
.product-card p {
    font-size: 16px;
    color: #666;
    font-weight: bold;
}

/* ‚úÖ Conteneur des boutons */
.buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: auto; /* ‚úÖ Pour aligner les boutons en bas */
}

/* ‚úÖ Style des boutons */
.buttons button {
    background: black;
    color: white;
    border: none;
    padding: 10px;
    font-size: 14px;
    cursor: pointer;
    border-radius: 5px;
    transition: background 0.3s ease;
    flex: 1;
    margin: 5px;
}

.buttons button:hover {
    background: #f0eaea;
}
.filters-sidebar {
    width: 250px;
    padding: 20px;
    background-color: white;
    border-right: 1px solid #ddd;
    height: 100vh; /* ‚úÖ Hauteur √©cran */
    position: sticky; /* ‚úÖ Reste visible m√™me au scroll */
    top: 0; /* ‚úÖ Fixe la position */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  
  }
  .filters-sidebar select,
  .filters-sidebar input {
    display: block;
    width: 100%;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 0.5rem;
  }
  .page-content {
    display: flex;
    align-items: flex-start;
    gap: 20px;
    padding: 0 20px;
    min-height: 100vh; /* ‚úÖ Prend toute la hauteur de l'√©cran */
    box-sizing: border-box;
}
.beauty-page {
    background-color: white;
}
